
.navbar {
  display: flex;
  /* background-color: var(--grey-color); */
  background: transparent;
  color: white;
  width: 100%;
  height: 70px;
  position: fixed;
  z-index: 10;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  top: 0;
  left: 0;
  transition: background 0.3s ease, box-shadow 0.3s ease;
  /* position: absolute; */

}

.navbar.scrolled {
  background: linear-gradient(135deg, #0f172a, #1e293b); /* solid color after scroll */
 box-shadow: 0 2px 8px rgba(0,0,0,0.4);
}

.social-media {
  display: none; /* hide by default */
}


/* Add these new rules */
.logo {
  display: flex;
  align-items: baseline; /* Aligns the text nicely at the bottom */
  cursor: pointer;
}

.logo-mr {
  font-family: 'Dancing Script', cursive;
  font-size: 1.6rem; /* Adjust size as needed */
  font-weight: 700;
  color: var(--orange-color); /* Makes "Mr." stand out */
  margin-right: 5px;
}

.logo-name {
  font-size: 3rem; /* Adjust size as needed */
  color: white;
}

/* Desktop navigation */
.navigation {
  display: flex;
  align-items: center;
  list-style: none;
  font-size: 20px;
}

.navigation a {
  text-decoration: none;
  color: white;
}

.navigation li {
  margin-right: 40px;
}

/* Hamburger menu styles */
.hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  margin: 0 40px;
  cursor: pointer;
}

.hamburger span {
  height: 5px;
  width: 100%;
  background: white;
  border-radius: 2px;
  transition: all 0.3s ease;
}

/* Hamburger animation */
.hamburger.open span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 6px);
}

.hamburger.open span:nth-child(2) {
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
}

/* Prevent scrolling when menu is open */
.no-scroll {
  overflow: hidden;
  /* height: 100vh; */
}


/* Responsive dropdown menu */
@media screen and (max-width: 768px) {
  /* Hide desktop nav */
 

  /* Change this rule */
.navigation {
  flex-direction: column;
  align-items: center;
  background: linear-gradient(to right, #182c50, #141d30);
  position: fixed;
  top: 70px;
  right: -100%; /* hidden off the screen */
  width: 60%; /* Changed from 50% to 60% */
  height: 100vh; /* full height of viewport */
  transition: right 0.4s ease;
  padding: 20px;
}

  .links.active .navigation {
    right: 0; /* slides into view */
  }

  .navigation li {
    margin: 15px 0;
  }

  /* Show hamburger on mobile */
  .hamburger {
    display: flex;
  }
  .social-media {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
    font-size: 25px;
  }

  .social-media a {
    color: white;
  }
}

/* --- Add this at the end of the file --- */
@media screen and (max-width: 480px) {
  .navigation {
    width: 75%; /* Make menu wider on small devices */
  }
}
